.offers-wrapper {
  grid-column: 1 / 10;
  grid-row: 1;
  border-right: 1.5px dashed black;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  background-color: #fcfcfc;

  display: flex;
  flex-direction: column;
  align-items: center;
  //border: 1px solid teal;

  h2 {
    margin-bottom: 1rem;
    padding: 0;
    color: #f75d37;
    font-weight: 600;
  }

  ul {
    padding-top: 1rem;
    width: auto;
    height: 43.84px;
    display: flex;
    margin: 0 auto;

    button {
      outline: none;
      border: none;
      background: transparent;
      font-size: 1.6rem;
      width: 2rem;

      &:hover {
        cursor: pointer;
        color: #f75d37;
      }
    }
  }

  .close-circle-outline {
    display: flex;
    width: 98%;
    height: 96.07px;
    align-items: center;
  }

  .close-circle-outline ion-icon {
    margin-left: auto;
    margin-bottom: 1.5rem;
    color: #f75d37;
    font-size: 1.9vw;
    --ionicon-stroke-width: 20px;
    &:hover {
      cursor: pointer;
      opacity: 0.5;
    }
  }
}

.display {
  border-radius: 30px;
  //background-color: #f5f5f5;
  width: 97%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  //border: 1px solid red;
}

.dddd {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  
  //border: 1px solid red;
}

.page__under-img {
  margin-top: 2rem;
  width: 50%;
  height: 50%;
}
